<template>
	<div class="exam">
		<el-button type="primary" class="addbtn" @click="confirmshow"></el-button>
		<div class="filtrate">
			<el-select v-model="courseid" @change="filtrate" placeholder="">
			    <el-option label="" value=""> </el-option>
			    <el-option v-for="item in courselist" :key="item.id" :label="item.name" :value="item.id"> </el-option>
			</el-select>
			<el-select v-model="typevalue" @change="filtrate" placeholder="">
			    <el-option v-for="item in typelist" :key="item.value" :label="item.label" :value="item.value"> </el-option>
			</el-select>
			<el-select v-model="modelvalue" @change="filtrate" placeholder="">
			    <el-option v-for="item in modellist" :key="item.value" :label="item.label" :value="item.value"> </el-option>
			</el-select>
			<el-select v-model="statusvalue" @change="filtrate" placeholder="">
			    <el-option v-for="item in statuslist" :key="item.value" :label="item.label" :value="item.value"> </el-option>
			</el-select>
			<el-select v-model="timevalue" @change="filtrate" placeholder="">
			    <el-option v-for="item in timelist" :key="item.value" :label="item.label" :value="item.value"> </el-option>
			</el-select>
		</div>
		<div class="table">
			<el-table :data="examlist" @row-click="checkexam" style="width: 100%">
			  <el-table-column width="80px">
		      	<template scope="scope">
		      		<span class="labelbox bgpurple" v-if="scope.row.model==''"></span>
		      		<span class="labelbox bggreen" v-if="scope.row.model==''"></span>
		      		<span class="labelbox bgblack" v-if="scope.row.model==''"></span>
			    </template>
		      </el-table-column>
		      <el-table-column label="">
		      	<template scope="scope">
			        <span style="margin-left: 10px">{{ scope.row.name }}</span>
			    </template>
		      </el-table-column>
		      <el-table-column width="120px">
		      	<template scope="scope">
			        <span class="namelabel bgblue" v-if="scope.row.status==0"></span>
			        <span class="namelabel bggreen" v-if="scope.row.status==1"></span>
			        <span class="namelabel bgorange" v-if="scope.row.status==2"></span>
			        <span class="namelabel bggray" v-if="scope.row.status==3"></span>
			    </template>
		      </el-table-column>
		      <el-table-column prop="courseName" label=""></el-table-column>
		      <el-table-column prop="type" label=""></el-table-column>
		      <el-table-column prop="time" label="" width="180px"></el-table-column>
		      <el-table-column prop="site" label="">
		      	<template scope="scope">
			        <span style="margin-left: 10px" v-for="item in scope.row.place">{{item.placename}}</span>
			    </template>
		      </el-table-column>
		      <el-table-column label="" width="150px">
		      	<template scope="scope">
					<el-button size="small" type="primary" @click.stop="editexam(scope.row)" v-if="scope.row.status==0&&teacherid==scope.row.creator"></el-button>
					<el-button size="small" type="danger" @click.stop="deleteexambtn(scope.row.examid)" v-if="scope.row.status==0&&teacherid==scope.row.creator"></el-button>
				</template>
		      </el-table-column>
		    </el-table>
		    <el-pagination class="page" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20, 30, 50]" :page-size="pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total">
    		</el-pagination>
		</div>
		<!---->
		<el-dialog title="" :visible.sync="dialogFormVisible">
	      <el-form :model="form">
	      	<el-form-item label="" :label-width="formLabelWidth">
	          <el-input class="popinput" :maxlength="50" v-model="form.name" placeholder=""></el-input>
	        </el-form-item>
	        <el-form-item label="" :label-width="formLabelWidth">
	          <el-select v-model="form.courseitem"  placeholder="">
	            <el-option v-for="item in courselist" :key="item.id" :label="item.name" :value="item.id"> </el-option>
	          </el-select>
	        </el-form-item>
	        <el-form-item label="" :label-width="formLabelWidth">
	          <el-select v-model="form.typeitem" placeholder="">
			    <el-option v-for="item in typelist" :key="item.value" :label="item.label" :value="item" v-if="item.value!=-1"> </el-option>
	          </el-select>
	        </el-form-item>
	        <el-form-item label="" :label-width="formLabelWidth">
	          <el-select v-model="form.modelitem" placeholder="">
			    <el-option v-for="item in modellist" :key="item.value" :label="item.label" :value="item" v-if="item.value!=-1"> </el-option>
	          </el-select>
	        </el-form-item>
	      </el-form>
	      <div slot="footer" class="dialog-footer">
	        <el-button @click="dialogFormVisible = false"> </el-button>
	        <el-button type="primary" @click="dialogconfirm"> </el-button>
	      </div>
	    </el-dialog>
	    <!---->
		<el-dialog title="" :visible.sync="deleteshow">
	      <el-form :model="form">
	      	<el-form-item label=""></el-form-item>
	      </el-form>
	      <div slot="footer" class="dialog-footer">
	        <el-button @click="deleteshow = false"> </el-button>
	        <el-button type="primary" @click="deleteexam(deleteexamid)"> </el-button>
	      </div>
	    </el-dialog>
		
	</div>
</template>

<script>
import {post,setCookie,getCookie,setTitle} from '../../config/util'
import $ from 'jquery'
export default {
data() {
  	return {
    	//
	    dialogFormVisible: false,//pop
	    formLabelWidth: '120px',//pop
	    form: {//det
	        name: '',//
	        courseitem: '',//
	        typeitem: '',//
	        modelitem: '',//
	    },
  		courselist:[],//
  		typelist:[{value:'-1',label:''},{value:'0',label:''},{value:'1',label:''},{value:'2',label:''}],
//		modellist:[{value:'-1',label:''},{value:'0',label:''},{value:'1',label:''},{value:'2',label:''}],
  		modellist:[{value:'-1',label:''},{value:'1',label:''},{value:'2',label:''}],
  		//
  		courseid:'',//id
  		typevalue:'-1',//
  		modelvalue:'-1',//
  		statusvalue:'-1',//
  		timevalue:'-1',//
  		statuslist:[{value:'-1',label:''},{value:'0',label:''},{value:'1',label:''},{value:'2',label:''},{value:'3',label:''}],
  		timelist:[{value:'-1',label:''},{value:'0',label:''},{value:'1',label:''},{value:'2',label:''},{value:'3',label:''}],
	    examlist:[],//
	    currentPage:1,
  		pagesize:10,
  		total:0,
  		teacherid:getCookie('uid'),
	    deleteshow:false,
	    deleteexamid:''
  	}
},
mounted(){
	this.queryexam(this.currentPage,this.pagesize,this.courseid,this.modelvalue,this.typevalue,this.statusvalue,this.timevalue);
	this.queryCourse();
},
methods: {
	handleSizeChange(val) {//
		this.pagesize=val;
		this.queryexam(this.currentPage,this.pagesize,this.courseid,this.modelvalue,this.typevalue,this.statusvalue,this.timevalue);
	},
	handleCurrentChange(val) {//
		this.currentPage=val;
		this.queryexam(this.currentPage,this.pagesize,this.courseid,this.modelvalue,this.typevalue,this.statusvalue,this.timevalue);
	},
	confirmshow(){
		this.form= {
	        name: '',//
	        courseitem: '',//
	        typeitem: '',//
	        modelitem: ''//
	    }
		this.dialogFormVisible=true
	},
	dialogconfirm(){//
		var self=this;console.log(JSON.stringify(this.form))
		if (!self.form.name) {
	    	this.$message({
	          message: '',
	          type: 'warning'
	        });
	        return false
	    }
	    if (!self.form.courseitem) {
	    	this.$message({
	          message: '',
	          type: 'warning'
	        });
	        return false
	    }
	    if (!self.form.typeitem) {
	    	this.$message({
	          message: '',
	          type: 'warning'
	        });
	        return false
	    }
	    if (!self.form.modelitem) {
	    	this.$message({
	          message: '',
	          type: 'warning'
	        });
	        return false
	    }
	    if(self.form.modelitem.value==0||self.form.modelitem.value==1){//
	    	this.$router.push({
		      name:'examtheory',
		      query:{
		      	name:self.form.name,
		      	courseitem:self.form.courseitem,
		      	typevalue:self.form.typeitem.value,
		      	typelabel:self.form.typeitem.label,
		      	modelvalue:self.form.modelitem.value,
		      	modellabel:self.form.modelitem.label,
		      }
		    })
	    }
	    if(self.form.modelitem.value==2){//
	    	this.$router.push({
		      name:'examoperate',
		      query:{
		      	name:self.form.name,
		      	courseitem:self.form.courseitem,
		      	typevalue:self.form.typeitem.value,
		      	typelabel:self.form.typeitem.label,
		      	modelvalue:self.form.modelitem.value,
		      	modellabel:self.form.modelitem.label,
		      }
		    })
	    } 
	},
	queryCourse(){//
        var self=this;
		post('/cls/selectCourses',{
            command:"cls/selectCourses",
            sessionid:getCookie('sid'),
            loginid:getCookie('uid')
        }).done((res)=>{
            if(res.errcode==0){
                console.log(res)
                self.courselist=res.courseslist;
            }
        })
	},
	filtrate(){//
		console.log(this.currentPage,this.pagesize,'courseid:'+this.courseid,'modelvalue:'+this.modelvalue,'typevalue:'+this.typevalue,'statusvalue:'+this.statusvalue,'timevalue:'+this.timevalue)
		this.queryexam(this.currentPage,this.pagesize,this.courseid,this.modelvalue,this.typevalue,this.statusvalue,this.timevalue);
	},
	queryexam(currentPage,pagesize,courseid,model,type,status,time){//
		var self=this;
		post('/unifyexam/queryExam',{
            command:"unifyexam/queryExam",
            loginid:getCookie('uid'),
            sessionid:getCookie('sid'),
            teacherid:getCookie('uid'),
            status:status,
			courseid:courseid,
			model:model,
			type:type,
			pagenum:currentPage,
			pagesize:pagesize,
			time:time
        }).done((res)=>{
            if(res.errcode==0){
                console.log(res)
                self.examlist=res.examlist;
                self.total=res.totalcount;
            }
        })
	},
	checkexam(row){//
		if(row.model==''){//
	    	this.$router.push({
		      name:'det_quiztheory',
		      query:{
		      	examid:row.examid,
		      }
		    })
	    }
		if(row.model==''){//
	    	this.$router.push({
		      name:'det_quiztheory',
		      query:{
		      	examid:row.examid,
		      }
		    })
	    }
	    if(row.model==''){//
	    	this.$router.push({
		      name:'det_operate',
		      params:{
		      	examid:row.examid
		      }
		    })
	    } 
	},
	deleteexambtn(id){
		this.deleteshow=true;
		this.deleteexamid=id;
	},
	deleteexam(examid){//
		var self=this;
		post('/unifyexam/deleteUnifyExam',{
            command:"unifyexam/deleteUnifyExam",
            loginid:getCookie('uid'),
            sessionid:getCookie('sid'),
            examid:examid
        }).done((res)=>{
            if(res.errcode==0){
               self.queryexam(self.currentPage,self.pagesize,self.courseid,self.modelvalue,self.typevalue,self.statusvalue,self.timevalue);
				this.deleteshow=false;
            }
        })
	},
	editexam(row){//
		var self=this;console.log(JSON.stringify(row))
		if(row.model==''||row.model==''){//
	    	this.$router.push({
		      name:'editexamtheory',
		      query:{
		      	examid:row.examid,
		      }
		    })
	    }
	    if(row.model==''){//
	    	this.$router.push({
		      name:'editexamoperate',
		      query:{
		      	examid:row.examid,
		      }
		    })
	    } 
	}
},



}
</script>
<style scoped> 
.addbtn{
	margin: 10px 20px;
}
.filtrate{
	margin: 10px 20px;
}
.filtrate .el-select{
	margin: 0 10px 10px 0;
	width: 17%;
}
.exam .table{
	width: 95%;
    margin: auto;
    margin-bottom: 10px; 
}
.page{
	margin: 20px 0;
	text-align:center;
}
.popinput{
	width: 221px;
} 

.labelbox{
	border-radius: 50%;
	color: #fff;
	width: 30px;
	height: 30px;
	line-height: 30px;
	text-align: center;
	display: inline-block;
	margin: 0 10px;
}
.bgpurple{
	background-color: #9932cd;
}
.bgblack{
	background-color: #475669;
}
.bgorange{
	background-color: #ff9966;
}
.bggreen{
	background-color: #09f981;
}
.bgblue{
	background-color: #09d5f9;
}
.bggray{
	background-color: #a2a9aa;
}
.namelabel{
    color: #fff;
    padding: 3px 5px;	
    margin-left: 10px;
    border-radius: 5px;
}
</style>


