<template>
	<div class="examtheory">
		<p class="nav"><span class="blue" @click="goback">&</span> > </p>
		<div class="content">
			<p class="top">
				<span>{{examname}}</span>
				<span>{{coursename}}</span>
				<span>{{type|typefn}}</span>
				<span>{{model|modelfn}}</span>
				<span>{{time}}</span>
				<span>{{papername}}</span>
			</p>
			<div class="bottom">
				<div class="line">
					<el-table :data="stuscorelist" border style="width: 100%">
				      <el-table-column type="index" label="" width="150"></el-table-column>
				      <el-table-column prop="name" label=""></el-table-column>
				      <el-table-column prop="username" label=""></el-table-column>
				      <el-table-column prop="score" label=""></el-table-column>
				      <el-table-column label="">
				      	<template scope="scope">{{scope.row.status|statusfn}} </template>
				      </el-table-column>
				      <el-table-column prop="gradetime" label=""></el-table-column>
				      <el-table-column label="">
				      	<template scope="scope">
					        <el-button size="small" @click="checkpaper(scope.row)"></el-button>
					    </template>
				      </el-table-column>
				    </el-table>
			    </div>
			    <el-pagination class="page" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentpage" :page-sizes="[10, 20, 30, 50]" :page-size="pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total">
	    		</el-pagination>
	    		<p class="page"><el-button @click="goback"></el-button></p>
			</div>
		</div>
	</div>
</template>

<script>
import {post,setCookie,getCookie,setTitle} from '../../config/util'
import $ from 'jquery'
export default {
	
data() {
  	return {
  		examid:this.$route.query.examid,//
  		time:'',//
  		papername:'',//
  		coursename:'',//
  		type:'',//
  		model:'',//
  		examname:'',//
  		stuscorelist:[],//
  		currentpage:1,
  		pagesize:10,
  		total:10,
  	}
},
filters:{
	statusfn:function(value){
		var status=value;
		switch (value){
			case -1: status="";
				break;
			case 0: status="";
				break;
			case 1: status="";
				break;
			case 2: status="";
				break;
			default:
				break;
		}
		return status;
	},
	typefn:function(value){
		var type=value;
		switch (value){
			case 0: type="";
				break;
			case 1: type="";
				break;
			case 2: type="";
				break;
			case 3: type="";
				break;
			default:
				break;
		}
		return type;
	},
	modelfn:function(value){
		var model=value;
		switch (value){
			case 0: model="";
				break;
			case 1: model="";
				break;
			case 2: model="";
				break;
			default:
				break;
		}
		return model;
	}
},
mounted(){
	this.queryexamdetail();
	this.querystuscorelist(this.currentpage,this.pagesize);
},
methods:{
	handleSizeChange(val) {//
	    this.pagesize=val;
		this.querystuscorelist(this.currentpage,this.pagesize);
	},
	handleCurrentChange(val) {//
	    this.currentpage=val;
		this.querystuscorelist(this.currentpage,this.pagesize);
	},
	queryexamdetail(){//
		var self=this;
		post('/unifyexam/queryexamdetail',{
            command:"unifyexam/queryexamdetail",
            loginid:getCookie('uid'),
            sessionid:getCookie('sid'),
            teacherid:getCookie('uid'),
            examid:self.examid
        }).done((res)=>{
            if(res.errcode==0){
            	self.time=res.starttime+'-'+res.endtime.slice(11,19);
            	self.papername=res.papername;
            	self.examname=res.name;
            	self.coursename=res.coursename;
            	self.type=res.type;
            	self.model=res.model;
            }
        })
	},
	querystuscorelist(currentpage,pagesize){//
		var self=this;
		post('/unifyexam/querystuscorelist',{
            command:"unifyexam/querystuscorelist",
            loginid:getCookie('uid'),
            sessionid:getCookie('sid'),
            teacherid:getCookie('uid'),
            examid:self.examid,
            pagenum:currentpage,
            pagesize:pagesize
        }).done((res)=>{
            if(res.errcode==0){
            	self.stuscorelist=res.scorelist;
            	self.total=res.totalcount;
            }
        })
	},
	goback(){//
		this.$router.push({
		    name:'exam_test',
		    query:{
		    	
		    }
	    })
	},
	checkpaper(row){//
		this.$router.push({
			name:"checkpaper",
			query:{
				examid:row.examid,
				paperid:row.paperid,
				stuid:row.stuid,
				name:row.name,
				username:row.username
			}
		})
	}
}

}
</script>

<style scoped>
.nav{
	font-size: 13px;
	margin: 5px 0;
}
.blue{
	color:#20a0ff;
	cursor: pointer;
}
.content{
	border: 1px solid #bfcbd9;
}
.top{
	border-bottom: 1px solid #bfcbd9;
	padding: 25px 0 0 25px;
}
.top span{
	margin: 0 50px 25px 0;
	width: 25%;
}
.bottom{
	padding-bottom: 50px;
}
.line{
	width: 90%;
	margin: 20px auto;
}
.page{
	margin: 40px 0;
	text-align: center;
}
</style>


