<template>
	<div class="examtheory">
		<p class="nav"><span class="blue">&</span> > </p>
		<div class="content">
			<p class="top">
				<span>{{this.row.name}}</span>
				<span>{{this.row.courseName}}</span>
				<span>{{this.row.type}}</span>
				<span>{{this.row.model}}</span>
				<span>'{{this.row.time}}'</span>
				<span v-if="this.row.model!=''">{{this.row.status | statusfn}}</span>
				<span v-if="this.row.model!=''">'{{this.row.name}}'</span>
			</p>
			<div class="bottom">
				<el-tabs v-model="activeName" @tab-click="handleClick">
				    <el-tab-pane label="1" name="first">
				    	<p class="line">3017</p>
				    	<p class="line">
				    		
				    		<el-button type="primary"></el-button>
				    		<el-button></el-button>
				    	</p>
				    	<div class="line">
				    		<el-table :data="list" border style="width: 100%">
						      <el-table-column type="index" label="" width="150"></el-table-column>
						      <el-table-column prop="name" label=""></el-table-column>
						      <el-table-column prop="num" label=""></el-table-column>
						      <!--<el-table-column prop="grade" label=""></el-table-column>
						      <el-table-column prop="statu" label=""></el-table-column>
						      <el-table-column prop="time" label=""></el-table-column>
						      <el-table-column label="">
						    	<template scope="scope">
									<el-button size="small" type="primary"></el-button>
									<el-button size="small" type="primary"></el-button>
								</template>
						      </el-table-column>	-->
						    </el-table>
				    	</div>
				    </el-tab-pane>
				    <el-tab-pane label="2" name="second">2</el-tab-pane>
				    <el-tab-pane label="3" name="third">3</el-tab-pane>
				</el-tabs>
			    <el-pagination class="page" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20, 30, 50]" :page-size="10" layout="total, sizes, prev, pager, next, jumper" :total="40">
	    		</el-pagination>
	    		<p class="page"><el-button></el-button></p>
			</div>
		</div>
	</div>
</template>

<script>
import {post,setCookie,getCookie,setTitle} from '../../config/util'
import $ from 'jquery'
export default {
	
data() {
  	return {
  		row:this.$route.query.row,
  		
		activeName:'first',
		list:[{name:'',num:'120612',grade:'100',statu:'',time:'2017-06-01 08:00:00'},{name:'',num:'120612',grade:'100',statu:'',time:'2017-06-01 08:00:00'},{name:'',num:'120612',grade:'100',statu:'',time:'2017-06-01 08:00:00'},{name:'',num:'120612',grade:'100',statu:'',time:'2017-06-01 08:00:00'},{name:'',num:'120612',grade:'100',statu:'',time:'2017-06-01 08:00:00'},{name:'',num:'120612',grade:'100',statu:'',time:'2017-06-01 08:00:00'},{name:'',num:'120612',grade:'100',statu:'',time:'2017-06-01 08:00:00'},{name:'',num:'120612',grade:'100',statu:'',time:'2017-06-01 08:00:00'},{name:'',num:'120612',grade:'100',statu:'',time:'2017-06-01 08:00:00'},{name:'',num:'120612',grade:'100',statu:'',time:'2017-06-01 08:00:00'},{name:'',num:'120612',grade:'100',statu:'',time:'2017-06-01 08:00:00'},],
		currentPage:1,
  	}
},
filters:{
	statusfn:function(value){
		var status=value;
		switch (value){
			case 0: status="";
				break;
			case 1: status="";
				break;
			case 2: status="";
				break;
			case 3: status="";
				break;
			default:
				break;
		}
		return status;
	}
},
mounted(){
	console.log(JSON.stringify(this.row))
	this.queryexamdetail();
},
methods:{
	handleClick(tab, event){
		console.log(tab, event);
	},
	handleSizeChange(val) {
	    console.log(` ${val} `);
	},
	handleCurrentChange(val) {
	    console.log(`: ${val}`);
	},
	queryexamdetail(){//
		var self=this;
		post('/unifyexam/queryexamdetail',{
            command:"unifyexam/queryexamdetail",
            loginid:getCookie('uid'),
            sessionid:getCookie('sid'),
            teacherid:getCookie('uid'),
            examid:self.row.examid
        }).done((res)=>{
            if(res.errcode==0){
            	
            }
        })
	}
}

}
</script>

<style scoped>
.nav{
	font-size: 13px;
	margin: 5px 0;
}
.blue{
	color:#20a0ff;
}
.content{
	border: 1px solid #bfcbd9;
}
.top{
	border-bottom: 1px solid #bfcbd9;
	padding: 25px 0 0 25px;
}
.top span{
	margin: 0 50px 25px 0;
	width: 25%;
}
.bottom{
	padding-bottom: 50px;
}
.bottom .el-tabs__header{
	border-bottom: 0;
}
.bottom .el-tabs{
	margin: 20px auto 0;
	width: 95%;
}
.line{
	margin-top: 20px;
}
.page{
	margin: 40px 0;
	text-align: center;
}
.el-table th,.el-table tr{
	text-align: center;
}
/*table tr th:first-child,table tr td:first-child{
	width: 100px;
}*/
</style>


