              currentPage: 1
            }
        },
        components: {
          areabox,
          courseinfo
        },
        mounted () {

        },
        methods: {
          filterTag(value, row) {
            return row.tag === value;
          },
          handleSizeChange(val) {
            console.log(` ${val} `);
          },
          handleCurrentChange(val) {
            console.log(`: ${val}`);
          }
        }
