  }
  .qbox{
    padding:8px;
  }
  .qcount{
    margin-top:16px;
  }
  .block{
    display:flex;
    justify-content: flex-end;
    padding-right:8px;
  }
  .smallgray{
    color:#aaa;
    font-size:12px;
  }
  .testname{
    width:200px;
    margin-left:10px;
  }
  .testtime{
    flex:1 1;
    text-align: right;
    padding-right:10px;
  }
</style>



