import flyer from './jquery.fly'
import xhttp from './http'

var methods={
  fly(from,to){
    var startoffset=$(from).offset()
    var endoffset=$(to).offset()
    var flyer=$('<div class="flyimage"></div>')
    flyer.fly({
      start: {
        left: startoffset.left,//
        top: startoffset.top //
      },
      end: {
        left: endoffset.left + 10,//
        top: endoffset.top + 10, //
      },
      onEnd: function() {
        $(flyer).remove(); //
      }
    })
  },
  deleteItem(arr,num){//index
    delete arr[num];
    var temp=[];
    for(var index in arr){
      temp.push(arr[index])
    };
    return temp
  },
  numtocase(val){//0-7
    switch (val){
      case 0:
        return 'A'
        break;
      case 1:
        return 'B'
        break;
      case 2:
        return 'C'
        break;
      case 3:
        return 'D'
        break;
      case 4:
        return 'E'
        break;
      case 5:
        return 'F'
        break;
      case 6:
        return 'G'
        break;
      case 7:
        return 'H'
        break;
    }
  }

}


export default methods



