<template>
	<div class="examtheory">
		<p class="nav"><span class="blue" @click="cancel">&</span> > </p>
		<div class="content">
			<p class="top">
				<span>{{detexamname}}</span>
				<span>{{detexamcoursename}}</span>
				<span>{{detexamtype|typefn}}</span>
				<span>{{detexammodel|modelfn}}</span>
			</p>
			<div class="bottom">
				<p class="cell_line">
					<span>:</span>
					<el-input v-model="examname" placeholder="" class="cell_right" style="padding: inherit;width: 350px;"></el-input>
				</p>
				<p class="cell_line">
					<span>:</span>
					<el-select v-model="examcourseid"  placeholder="" class="cell_right" style="padding: inherit;width: 350px;">
			            <el-option v-for="item in courselist" :key="item.id" :label="item.name" :value="item.id"> </el-option>
			        </el-select>
				</p>
				<p class="cell_line">
					<span>:</span>
					<el-select v-model="examtype" placeholder="" class="cell_right" style="padding: inherit;width: 350px;">
					    <el-option v-for="item in typelist" :key="item.value" :label="item.label" :value="item.value" > </el-option>
			        </el-select>
				</p>
				<p class="cell_line">
					<span>:</span>
					<label class="cell_right box" style="border: none;">{{exammodel|modelfn}}</label>
				</p>
				<p class="cell_line">
					<span>:</span>
					<el-date-picker v-model="timeval1" @change="stimechange" type="datetime" placeholder="" :picker-options="pickerOptions" class="cell_right" style="padding: inherit;width: 195px;"></el-date-picker>
					<el-date-picker v-model="timeval2" @change="etimechange" type="datetime" placeholder="" :picker-options="pickerOptions" class="cell_right" style="padding: inherit;width: 195px;"></el-date-picker>
				</p>
				<p class="cell_line">
					<span>:</span>
					<label class="cell_right box">{{selectpaperitem.name}}</label><el-button type="primary" class="addbtn" @click="selectpaper"></el-button>
				</p>
				<p class="cell_line">
					<span>:</span>
					<label class="cell_right box"><p v-for="item in classselected">{{item.name}}</p> </label><el-button type="primary" class="addbtn" @click="selectstu"></el-button>
				</p>
				<p class="cell_line" v-if="false">
					<span>:</span>
					<el-radio-group class="cell_right" v-model="radio">
					    <el-radio :label="0"></el-radio>
					    <el-radio :label="1"></el-radio>
					</el-radio-group>
				</p>
				<p class="cell_line">
					<span>:</span>
					<label class="cell_right box"><p v-for="item in siteselected">{{item.name}}</p></label><el-button type="primary" class="addbtn" @click="selectsite"></el-button>
				</p>
				<p class="cell_line" style="text-align: center;">
					<el-button type="primary" @click="save"></el-button>
					<el-button @click="cancel"></el-button>
				</p>
			</div>
		</div>
		<!---->
		<el-dialog  title="" :visible.sync="dialogSelStu" :close-on-click-modal="false" @close="">
            <el-transfer
			    filterable
			    :filter-method="filterclass"
			    filter-placeholder=""
			    v-model="valueclass"
			    :data="dataclass"
			    style="margin: auto;width: 460px;">
			</el-transfer>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogSelStu = false"> </el-button>
                <el-button type="primary" @click="sureclass"> </el-button>
            </div>
        </el-dialog>
        <!---->
		<el-dialog  title="" :visible.sync="selectpapershow" :close-on-click-modal="false" @close="">
			<p class="popcontent">
				<el-select v-model="courseid" placeholder="">
				    <el-option label="" value=""> </el-option>
				    <el-option v-for="item in courselist" :key="item.id" :label="item.name" :value="item.id"> </el-option>
				</el-select>
				<el-date-picker v-model="papertime" @change="papertimechange" type="daterange" align="cell_right" placeholder="" :picker-options="pickerOptions"> </el-date-picker>
				<el-input class="searchbox" placeholder="" icon="search" v-model="papername" :on-icon-click="searchpaper"></el-input>
			</p>
			<div class="popcontent">
				<div class="table">
					<el-table @row-click="checkpaper" :data="paperlist" border style="width: 100%">
				      <el-table-column label="">
				      	<template scope="scope">
				      		<div class="labelbox bgpurple" v-if="scope.row.type==1"></div>
		      				<div class="labelbox bgblack" v-if="scope.row.type==2"></div>
				      		<div class="labelbox bgorange" v-if="scope.row.type==5"></div>
				      		<span>{{scope.row.name}}</span>
				      	</template>
				      </el-table-column>
				      <el-table-column prop="operatortime" label=""></el-table-column>
				    </el-table>
		    		<el-pagination class="pagebox" layout="prev, pager, next" :total="total" @current-change="handleCurrentChange" > </el-pagination>
				</div>
			</div>
			</p>
		</el-dialog>
		<!---->
		<el-dialog  title="" :visible.sync="selectsiteshow" :close-on-click-modal="false" @close="">
			<p class="popcontent">
				<el-transfer
				    filterable
				    :filter-method="filterMethod"
				    filter-placeholder=""
				    v-model="value"
				    :data="data"
			    	style="margin: auto;width: 460px;">
				</el-transfer>
			</p>
			<p class="popcontent" style="text-align: center;margin-top: 20px;">
				<el-button type="primary" @click="sure"></el-button>
				<el-button @click="closepop"></el-button>
			</p>
		</el-dialog >
	</div>
</template>

<script>
import {post,setCookie,getCookie,setTitle} from '../../config/util'
import breadcrumb from '../../components/breadcrumb'
import translist from '../../components/translist'
import $ from 'jquery'
export default {
	
data() {
  	return {
		examid:this.$route.query.examid,//id
		examname:'',//
		examcourseid:'',//
		examcoursename:'',//
		examtype:'',//
		exammodel:'',//
		detexamname:'',//
		detexamcoursename:'',//
		detexamtype:'',//
		detexammodel:'',//
		typelist:[{value:0,label:''},{value:1,label:''},{value:2,label:''},{value:3,label:''}],
		timeval1:'',//value
		timeval2:'',//value
		
  		starttime:'',//
        endtime:'',//
        selectpapershow:false,//pop
        selectpaperitem:{
        	name:'',
        	paperid:'',
        },//item
        papertime:'',//
  		currentpage:1,//
		pagesize:10,//
  		total:0,//
  		paperlist:[],//
        courselist:[],//
        courseid:'',//id
        paperstartime:'',//
        paperendtime:'',//
        papername:'',//
  		pickerOptions: {//
          disabledDate(time) {
	            return time.getTime() < Date.now() - 8.64e7;
	        }
        },
        dialogSelStu:false,//pop
        
        dataclass:[],//pop
        valueclass:[],//id
        filterclass(query, item) {//
          return item.rooms.indexOf(query) > -1;
        },
        classlist:[],//
		classselected:[],//
        
  		selectsiteshow:false,//pop
		data:[],//pop
        value: [],//id
        filterMethod(query, item) {//
          return item.sites.indexOf(query) > -1;
        },
		radio:'',//
        sitelist:[],//
        siteselected:[],//
  	}
},
components: {
    breadcrumb,
    translist,
},
filters:{
	typefn:function(value){
		var status=value;
		switch (value){
			case 0: status="";
				break;
			case 1: status="";
				break;
			case 2: status="";
				break;
			case 3: status="";
				break;
			default:
				break;
		}
		return status;
	},
	modelfn:function(value){
		var status=value;
		switch (value){
			case 0: status="";
				break;
			case 1: status="";
				break;
			case 2: status="";
				break;
			default:
				break;
		}
		return status;
	}
},
mounted(){
//	if(this.row.model==''){
//		this.radio=0
//	}
	this.getAllClass();
	this.queryCourse();
	this.queryexamdetail();
},
methods:{
	queryexamdetail(){//
		var self=this;
		post('/unifyexam/queryexamdetail',{
            command:"unifyexam/queryexamdetail",
            loginid:getCookie('uid'),
            sessionid:getCookie('sid'),
            teacherid:getCookie('uid'),
            examid:self.examid
        }).done((res)=>{
            if(res.errcode==0){
            	self.examname=res.name;
				self.examcoursename=res.coursename;
				self.examtype=res.type; 
				self.exammodel=res.model;
				self.detexamname=res.name;
				self.detexamcoursename=res.coursename;
				self.detexamtype=res.type; 
				self.detexammodel=res.model;
            	self.examcourseid=res.courseid;
				self.timeval1=res.starttime;
				self.timeval2=res.endtime;
            	self.selectpaperitem.name=res.papername;
				self.selectpaperitem.paperid=res.paperid;
				for (var i=0;i<res.classlist.length;i++) { 
					var obj={
						ctype:'1',
						name:res.classlist[i].classname,
						id:res.classlist[i].classid
					}
					self.classselected.push(obj);
					self.valueclass.push(obj.id)
				}
				for(var i=0;i<res.placelist.length;i++){
					var obj={
						name:res.placelist[i].placename,
						id:res.placelist[i].placeid
					}
					self.siteselected.push(obj)
					self.value.push(obj.id)
				}
            }
        })
	},
	
	queryCourse(){//
        var self=this;
		post('/cls/selectCourses',{
            command:"cls/selectCourses",
            sessionid:getCookie('sid'),
            loginid:getCookie('uid')
        }).done((res)=>{
            if(res.errcode==0){
                self.courselist=res.courseslist;
            }
        })
	},
	querysite(){//
		var self=this;
		post('/site/listplace',{
            command:"site/listplace",
			sessionid:getCookie('sid'),
			loginid:getCookie('uid'),
			pagenum:1,
			pagepersize:1000,
        }).done((res)=>{
            if(res.errcode==0){
				var sites=[];
				self.sitelist=res.placelist;
				var id=[];
				for (var i=0;i<res.placelist.length;i++) {
					sites.push(res.placelist[i].name)
					id.push(res.placelist[i].id)
				}
				sites.forEach((site, index) => {
			      self.data.push({
			        label: site,
			        key: id[index],
			        sites: sites[index]
			      });
			  });
            }
        })
	},
	querypaper(currentpage,pagesize,courseid,starttime,endtime,name){//
		var self=this;
		post('/exampaper/querypaper',{
            command:"exampaper/querypaper",
			sessionid:getCookie('sid'),
			loginid:getCookie('uid'),
			operator:getCookie('uid'),
			page:currentpage,
			reqnum:pagesize,
			majorid:courseid,
			name:name,
			type:6,
			starttime:starttime,
			endtime:endtime
        }).done((res)=>{
            if(res.errcode==0){
                self.paperlist=res.paperlist;
                self.total=res.totalcount;
            }
        })
	},
	getAllClass(){//
        let self = this;
        post('/unifyexam/queryclasslist',{
            command:'unifyexam/queryclasslist',
            sessionid:getCookie('sid'),
            loginid:getCookie('uid'),
            teacherId:getCookie('uid'),
            stuId:''
        }).done((data)=>{
            if(data.errcode==0){
                self.classlist=data.classlist;
            	var rooms=[];
                var id=[];
                for (var i=0;i<data.classlist.length;i++) {
					rooms.push(data.classlist[i].name)
					id.push(data.classlist[i].id)
				}
                rooms.forEach((room, index) => {
			      self.dataclass.push({
			        label: room,
			        key: id[index],
			        rooms: rooms[index]
			      });
			    });
			}
        })
    },
	/*********************************************************************************/ 
    selectstu(){
		this.dialogSelStu=true;
		this.dataclass=[];
		this.getAllClass();
	},
	sureclass(){//
		var self=this;
		self.classselected=[];
		for (var i=0;i<self.valueclass.length;i++) {
			for(var j=0;j<self.classlist.length;j++){
				if (self.valueclass[i]==self.classlist[j].id) {
					self.classselected.push({
						name:self.classlist[j].name,
						id:self.classlist[j].id
					})
				}
			}
		}
		this.dialogSelStu=false;
	},
    /*******************************************************************************************/
    closepop(){//
		this.selectpapershow=false;
		this.selectsiteshow=false;
	},
	stimechange(val){//
		var nowtime=Date.parse(new Date())/1000;
		var stime=Date.parse(val)/1000;
        if(stime>nowtime){
			this.starttime=val;
        }else{
			this.$message({
	          showClose: true,
	          message: '!',
	          type: 'error'
	        });
	        this.timeval1='';
			return false;
        }
	},
	etimechange(val){//
		if(!this.starttime){
			this.$message({
	          showClose: true,
	          message: '!',
	          type: 'error'
	       });
			return false;
		}
		var stime=Date.parse(this.starttime)/1000;
		var etime=Date.parse(val)/1000;
        if(etime>stime){
			this.endtime=val
        }else{
			this.$message({
	          showClose: true,
	          message: '!',
	          type: 'error'
	        });
	        this.timeval2='';
			return false;
        }
	},
	selectpaper(){//pop
		this.selectpapershow=true;
	    this.querypaper(this.currentpage,this.pagesize,this.courseid,this.paperstarttime,this.paperendtime,this.papername)
	},
	handleCurrentChange(val) {//
	    this.currentpage=val;
	    this.querypaper(this.currentpage,this.pagesize,this.courseid,this.paperstarttime,this.paperendtime,this.papername)
	},
	papertimechange(val){//
		this.paperstarttime=val.slice(0,11);
		this.paperendtime=val.slice(12,23);
	    this.querypaper(this.currentpage,this.pagesize,this.courseid,this.paperstarttime,this.paperendtime,this.papername)
	},
	searchpaper(){//
	    this.querypaper(this.currentpage,this.pagesize,this.courseid,this.paperstarttime,this.paperendtime,this.papername)
	},
	checkpaper(row){//
		this.selectpaperitem.name=row.name;
		this.selectpaperitem.paperid=row.paperid;
		this.selectpapershow=false;
	},
    /*******************************************************************************************/
	selectsite(){
		this.selectsiteshow=true;
		this.data=[];
		this.querysite();
	},
	sure(){//
		var self=this;
		self.siteselected=[];
		for (var i=0;i<self.value.length;i++) {
			for(var j=0;j<self.sitelist.length;j++){
				if (self.value[i]==self.sitelist[j].id) {
					self.siteselected.push({
						name:self.sitelist[j].build+self.sitelist[j].floor+self.sitelist[j].name,
						id:self.sitelist[j].id
					})
				}
			}
		}
		this.selectsiteshow=false;
	},
	save(){//
        let self = this;
        if(!self.examname){
        	this.$message({
	          showClose: true,
	          message: '!',
	          type: 'error'
	        });
			return false;
        }
        if(!self.examcourseid){
        	this.$message({
	          showClose: true,
	          message: '!',
	          type: 'error'
	        });
			return false;
        }
        if(!self.starttime&&!self.endtime){
        	this.$message({
	          showClose: true,
	          message: '!',
	          type: 'error'
	        });
			return false;
        }
        var stime=Date.parse(this.starttime)/1000;
		var etime=Date.parse(this.endtime)/1000;
        if(etime<stime){
			this.$message({
	          showClose: true,
	          message: '!',
	          type: 'error'
	        });
	        this.timeval2='';
			return false;
        }
        if(!self.selectpaperitem){
        	this.$message({
	          showClose: true,
	          message: '!',
	          type: 'error'
	        });
			return false;
        }
        if(self.classselected.length==0){
        	this.$message({
	          showClose: true,
	          message: '!',
	          type: 'error'
	        });
			return false;
        }
        if(self.siteselected.length==0){
        	this.$message({
	          showClose: true,
	          message: '!',
	          type: 'error'
	        });
			return false;
        }
        
        if (self.startime==''&&self.endtime=='') {
        	this.starttime=self.time.slice(0,19);
			this.endtime=self.time.slice(22,41);
        }
        if(self.value.length==0){
        	for (var i=0;i<self.siteselected.length;i++) {
        		self.value.push(self.siteselected[i].id)	
        	}
        }
        post('/unifyexam/modifyexam',{
            command:'unifyexam/modifyexam',
            sessionid:getCookie('sid'),
            loginid:getCookie('uid'),
            teacherid:getCookie('uid'),
            examid:self.examid,
            name:self.examname,
            courseid:self.examcourseid,
            model:self.exammodel,
            type:self.examtype,
            starttime:self.starttime,
            endtime:self.endtime,
            answermode:self.radio,
            paperid:self.selectpaperitem.paperid,
            classlist:self.valueclass,
            placelist:self.value,
            itemteacherlist:[],
        }).done((data)=>{
            if(data.errcode==0){
                this.$router.push({
				    name:'exam_test',
				    query:{
				    	
				    }
			    })
            }else{
            	this.$message({
		          showClose: true,
		          message: data.errdesc,
		          type: 'error'
		        });
				return false;
            }
        })
	},
	cancel(){//
		history.go(-1)
	}
},
watch:{
	courseid:function(val){
		this.querypaper(this.currentpage,this.pagesize,this.courseid,this.paperstarttime,this.paperendtime,this.papername)
	}
}

}
</script>

<style scoped>
.nav{
	font-size: 13px;
	margin: 5px 0;
}
.blue{
	color:#20a0ff;
	cursor: pointer;
}
.content{
	border: 1px solid #bfcbd9;
}
.top{
	border-bottom: 1px solid #bfcbd9;
	padding: 25px 30px;
}
.top span{
	margin-right: 70px;
}
.bottom{
	padding: 100px 0;
}
.cell_line{
	text-align: left;
    margin-bottom: 30px;
    width: 600px;
    margin: 0 auto 30px;
}
.cell_line span{
	width: 100px;
	text-align: right;
}
.addbtn{
	margin-left: 10px;
}
.cell_right{
	margin-left: 30px;
    vertical-align: middle;
    line-height: 35px;
    min-height: 35px; 
    height: auto;
    width: 310px;
    padding: 0 20px;
}
.box{
	border: 1px solid #bfcbd9;
	border-radius: 4px;
}
.popbox{
    position: fixed;	
	background-color: #fff;
    z-index: 1;
    border: 1px solid #bfcbd9;
    width: 700px;
    height: 600px;
    left: 25%;
    top: 100px;
    padding: 10px;
}
.poptitle{
	border-bottom: 1px solid #bfcbd9;
	position: relative;
	padding: 10px 0;
}
.poptitle i{
	position: absolute;
    right: 0;
    cursor: pointer;
}
.popcontent{
	margin: 10px 0;
	text-align: left;
	width: 100%;
}
.popcontent .el-input{
	width: 200px;
}
.popcontent .el-select{
	width: 100px;
}
.popcontent_table{
	height: 460px;
	overflow: auto;
}
.pagebox{
	margin: 10px 0;
	text-align: center;
}
.popbtn{
	text-align: center;
	margin-bottom: 30px;
}
.bgpurple{
	background-color: #9932cd;
}
.bgblack{
	background-color: #475669;
}
.bgorange{
	background-color: #ff9966;
}
.labelbox{
	border-radius: 50%;
	color: #fff;
	width: 30px;
	height: 30px;
	line-height: 30px;
	text-align: center;
	display: inline-block;
	margin: 0 10px;
}
.transfer-footer {
    margin-left: 20px;
    padding: 6px 5px;
  }
</style>


