<template>
  <div class="container">
    <cpagetitle :title="pagetitletext" :showback="true"></cpagetitle>

    <cmain :title="maintitle[0]">
      <div class="maincontent">
        <el-input type="textarea" placeholder=",300" v-model="qname" :maxlength='qtitlemax'></el-input>
        <el-upload
          class="upload-demo mt10"
          action="https://jsonplaceholder.typicode.com/posts/"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :file-list="fileList">
          <el-button size="small" type="primary"></el-button>
          <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
        </el-upload>
      </div>
    </cmain>

    <div class="zuhehandler">
      <el-button type="primary" @click="adddanxuan()"></el-button>
      <el-button type="primary" @click="addduoxuan()"></el-button>
      <el-button type="primary" @click="addpanduan()"></el-button>
      <el-button type="primary" @click="addwenda()"></el-button>
    </div>

    <ul>
      <li v-for="(value1,index1) in subitem" class="oneq">

        <div class="danxuan" v-if="subitem[index1].cateid==1">
          <span class="deleteoneq el-icon-minus optionhandler" @click="deleteoneq(index1)"></span>
          <cmain :title="''+(index1+1)+'-'">
            <div class="maincontent">
              <el-input type="textarea" placeholder=",300" v-model="subitem[index1].itemquestion" :maxlength='qtitlemax'></el-input>
            </div>
          </cmain>
          <cmain :title="maintitle[1]">
            <ul class="addoptions maincontent">
              <li v-for="(value2,index2) in subitem[index1].questionitem">
                <span class="itemname">{{ index2 | numtoa }}</span>
                <el-input placeholder="100" v-model="value2.content" :maxlength='qcontentmax'></el-input>
                <b @click="addoption(index1,index2)" v-show="value2.handler=='add'" class="el-icon-plus optionhandler"></b>
                <b @click="deleteoption(index1,index2,'danxuan')" v-show="value2.handler=='delete'" class="el-icon-minus optionhandler"></b>
              </li>
            </ul>
          </cmain>
          <cmain :title="maintitle[2]">
            <div class="maincontent">
              <el-radio v-for="(value3,index3) in subitem[index1].questionitem" class="radio" v-model="subitem[index1].answer" :label="index3 | numtoa">{{ index3 | numtoa }}</el-radio>
            </div>
          </cmain>
        </div>

        <div class="duoxuan" v-else-if="subitem[index1].cateid==2">
          <span class="deleteoneq el-icon-minus optionhandler" @click="deleteoneq(index1)"></span>
          <cmain :title="''+(index1+1)+'-'">
            <div class="maincontent">
              <el-input type="textarea" placeholder=",300" v-model="subitem[index1].itemquestion" :maxlength='qtitlemax'></el-input>
            </div>
          </cmain>
          <cmain :title="maintitle[1]">
            <ul class="addoptions maincontent">
              <li v-for="(value2,index2) in subitem[index1].questionitem">
                <span class="itemname">{{ index2 | numtoa }}</span>
                <el-input placeholder="100" v-model="value2.content" :maxlength='qcontentmax'></el-input>
                <b @click="addoption(index1,index2)" v-show="value2.handler=='add'" class="el-icon-plus optionhandler"></b>
                <b @click="deleteoption(index1,index2,'duoxuan')" v-show="value2.handler=='delete'" class="el-icon-minus optionhandler"></b>
              </li>
            </ul>
          </cmain>
          <cmain :title="maintitle[2]">
            <div class="maincontent">
              <el-checkbox-group v-model="subitem[index1].answer">
                <el-checkbox :label="index3 | numtoa" v-for="(value3,index3) in subitem[index1].questionitem">{{ index3 | numtoa }}</el-checkbox>
              </el-checkbox-group>
            </div>
          </cmain>
        </div>

        <div class="panduan" v-else-if="subitem[index1].cateid==4">
          <span class="deleteoneq el-icon-minus optionhandler" @click="deleteoneq(index1)"></span>
          <cmain :title="''+(index1+1)+'-'">
            <div class="maincontent">
              <el-input type="textarea" placeholder=",300" v-model="subitem[index1].itemquestion" :maxlength='qtitlemax'></el-input>
            </div>
          </cmain>
          <cmain :title="maintitle[2]">
            <div class="maincontent">
              <el-radio class="radio" v-model="subitem[index1].answer" label="0"></el-radio>
              <el-radio class="radio" v-model="subitem[index1].answer" label="1"></el-radio>
            </div>
          </cmain>
        </div>

        <div class="wenda" v-else>
          <span class="deleteoneq el-icon-minus optionhandler" @click="deleteoneq(index1)"></span>
          <cmain :title="''+(index1+1)+'-'">
            <div class="maincontent">
              <el-input type="textarea" placeholder=",300" v-model="subitem[index1].itemquestion" :maxlength='qtitlemax'></el-input>
            </div>
          </cmain>
          <cmain :title="maintitle[2]">
            <div class="maincontent">
              <el-input type="textarea" placeholder=",300" v-model="subitem[index1].answer" :maxlength='qtitlemax'></el-input>
            </div>
          </cmain>
        </div>
      </li>
    </ul>

    <cmain :title="maintitle[3]">
      <div class="maincontent">
        <ul>
          <li class="flex">
            <span class="setlabel"></span>
            <div class="setcontent">
              <el-rate v-model="dificulty"></el-rate>
            </div>
          </li>
          <li class="flex mt10">
            <span class="setlabel"></span>
            <div class="setcontent">
              <el-select
                v-model="systag"
                multiple
                filterable
                placeholder="">
                <el-option
                  v-for="item in options4"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
            </div>
          </li>
          <li class="flex mt10">
            <span class="setlabel"></span>
            <div class="setcontent">
              <el-tree :data="data" :props="defaultProps" @node-click="handleNodeClick" highlight-current></el-tree>
            </div>
          </li>
        </ul>
      </div>
    </cmain>

    <cfooter :submitnewq="submitnewq" :action="action"></cfooter>
  </div>
</template>

<script>
  import methods from '../../assets/js/methods'
  import cmain from '../../components/areabox.vue'
  import cpagetitle from '../../components/cpagetitle'
  import cfooter from '../../components/cfooter'
  import xhttp from '../../assets/js/http'

  export default {
    data() {
      return {
        continue:true,//
        qname:'',//
        qtitlemax:300,
        qcontentmax:100,
        disciplinelist:[],//
        seldiscipline:'',//
        pagetitletext:'-',//
        maintitle:['-','','',''],//
        subitem:[],//
        questionmodel:{
          itemquestion:'',//,
          cateid:'',//
          questionitem:[{itemname:'',content:'',handler:'add'},{itemname:'',content:'',handler:'delete'},{itemname:'',content:'',handler:'delete'},{itemname:'',content:'',handler:'delete'},{itemname:'',content:'',handler:'delete'}],//
          answer:''//
        },
        fileList: [],
        //
        dificulty:null,//type:number
        //
        systag:[],
        options4: [],
        value9: [],
        list: [],
        
        states: ["Alabama", "Alaska", "Arizona",
          "Arkansas", "California", "Colorado",
          "Connecticut", "Delaware", "Florida",
          "Georgia", "Hawaii", "Idaho", "Illinois",
          "Indiana", "Iowa", "Kansas", "Kentucky",
          "Louisiana", "Maine", "Maryland",
          "Massachusetts", "Michigan", "Minnesota",
          "Mississippi", "Missouri", "Montana",
          "Nebraska", "Nevada", "New Hampshire",
          "New Jersey", "New Mexico", "New York",
          "North Carolina", "North Dakota", "Ohio",
          "Oklahoma", "Oregon", "Pennsylvania",
          "Rhode Island", "South Carolina",
          "South Dakota", "Tennessee", "Texas",
          "Utah", "Vermont", "Virginia",
          "Washington", "West Virginia", "Wisconsin",
          "Wyoming"],
        //
        data: [],
        defaultProps: {
          children: 'children',
          label: 'label'
        },
        action:'new',
      }
    },
    filters:{
      numtoa:function(val){
        switch (val){
          case 0:
            return 'A'
            break;
          case 1:
            return 'B'
            break;
          case 2:
            return 'C'
            break;
          case 3:
            return 'D'
            break;
          case 4:
            return 'E'
            break;
          case 5:
            return 'F'
            break;
          case 6:
            return 'G'
            break;
          case 7:
            return 'H'
            break;
        }
      }
    },
    components: {
      cmain,
      cpagetitle,
      cfooter
    },
    mounted () {
      if(this.$route.query.qid){
        this.pagetitletext='-';
        this.action='edit';
      }
      this.list = this.states.map(item => {
          return { value: item, label: item };
    })
      this.querydiscipline();
      this.gettags();
      this.gettree();
    },

    methods: {
      gettree(){
        var self=this
        var data={
          command:'question/questioncategorytree',
          sessionid: $.cookie('sid'),
          loginid:$.cookie('uid'),
        }
        function callback(res){
          self.data=res.children
        }
        xhttp.post('/question/questioncategorytree',data,callback,xhttp.errcodefn,xhttp.errfn)
      },
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      // remoteMethod(query) {
      //   if (query !== '') {
      //     this.loading = true;
      //     setTimeout(() => {
      //       this.loading = false;
      //     this.options4 = this.list.filter(item => {
      //         return item.label.toLowerCase()
      //           .indexOf(query.toLowerCase()) > -1;
      //   });
      //   }, 200);
      //   } else {
      //     this.options4 = [];
      //   }
      // },
      gettags(){
        var self=this
        var data={
          command:'question/querylablist',
          sessionid: $.cookie('sid'),
          loginid:$.cookie('uid'),
        }
        function callback(res){
          self.options4=res.lablist
        }
        xhttp.post('/question/querylablist',data,callback,xhttp.errcodefn,xhttp.errfn)
      },
      handleNodeClick(data) {
        console.log(data);
      },
      adddanxuan:function(){
        this.augments();
        if (this.continue){
          this.subitem.push({
            itemquestion:'',//,
            cateid:1,//
            questionitem:[{itemname:'',content:'',handler:'add'},{itemname:'',content:'',handler:'delete'},{itemname:'',content:'',handler:'delete'},{itemname:'',content:'',handler:'delete'},{itemname:'',content:'',handler:'delete'}],//
            answer:''//
          })
        }
      },
      addduoxuan:function(){
        this.augments();
        if (this.continue) {
          this.subitem.push({
            itemquestion: '',//,
            cateid: 2,//
            questionitem: [{itemname: '', content: '', handler: 'add'}, {
              itemname: '',
              content: '',
              handler: 'delete'
            }, {itemname: '', content: '', handler: 'delete'}, {
              itemname: '',
              content: '',
              handler: 'delete'
            }, {itemname: '', content: '', handler: 'delete'}],//
            answer: []//
          })
        }
      },
      addpanduan:function(){
        this.augments();
        if (this.continue) {
          this.subitem.push({
            itemquestion: '',//,
            cateid: 4,//
            answer: ''//
          })
        }
      },
      addwenda:function(){
        this.augments();
        if (this.continue) {
          this.subitem.push({
            itemquestion: '',//,
            cateid: 5,//
            answer: ''//
          })
        }
      },
      addoption:function(index1,index2){//
        if(this.subitem[index1].questionitem.length>=8){
          this.$message({
            type: 'warning',
            message: '8'
          })
          return
        }
        this.subitem[index1].questionitem.push({
          itemname:'',
          content:'',
          handler:'delete'
        })
      },
      augments:function (){
        if (this.subitem.length>9){
          this.$message({
            type: 'warning',
            message: ''
          })
          this.continue=false
        }else {
          this.continue=true
        }
      },
      deleteoption:function(index1,index2,type){//
        var self=this
        if(type=='danxuan'){//
          if(this.subitem[index1].questionitem.length<=1){
            this.$message({
              type: 'warning',
              message: '1'
            })
            return
          }
          self.subitem[index1].questionitem=methods.deleteItem(self.subitem[index1].questionitem,index2)
        }else{//2
          if(this.subitem[index1].questionitem.length<=2){
            this.$message({
              type: 'warning',
              message: '2'
            })
            return
          }
          self.subitem[index1].questionitem=methods.deleteItem(self.subitem[index1].questionitem,index2)
        }
      },
      querydiscipline:function(){//
        var self=this
        var data={
          command:"cls/selectCourses",
          sessionid: $.cookie('sid'),
          loginid:$.cookie('uid')
        }
        function callback(res){
          self.disciplinelist=res.courseslist
        }
        xhttp.post("/cls/selectCourses",data,callback,xhttp.errcodefn,xhttp.errfn)
      },
      submitnewq:function(){//
        if(this.qname==''){
          this.$message({
            type: 'warning',
            message: ''
          })
          return
        }
        if (this.subitem.length==0){
          this.$message({
            type: 'warning',
            message: ''
          })
          return
        }
        for(var i=0;i<this.subitem.length;i++){
          if(!this.subitem[i].itemquestion){
            this.$message({
              type: 'warning',
              message: ''
            })
            return
          }
          if(!this.subitem[i].answer && this.subitem[i].answer!=0){
            this.$message({
              type: 'warning',
              message: ''
            })
            return
          }
          if(this.subitem[i].cateid==1 || this.subitem[i].cateid==2){
            if(this.subitem[i].cateid==2){//
              var answerstr=''
              for(index in this.subitem[i].answer){
                answerstr+=this.subitem[i].answer[index]
              }
              this.subitem[i].answer=answerstr
            }
            for(var j=0;j<this.subitem[i].questionitem.length;j++){
              this.subitem[i].questionitem[j].itemname=methods.numtocase(j)
              if(this.subitem[i].questionitem[j].content==''){
                this.$message({
                  type: 'warning',
                  message: ''
                })
                return
              }
            }
          }
        }
        if(this.seldiscipline==''){
          this.$message({
            type: 'warning',
            message: ''
          })
          return
        }
        var data={
          command:'question/addassociationquestion',
          sessionid:$.cookie('sid'),
          loginid:$.cookie('uid'),
          creatorid:$.cookie('uid'),
          name:this.qname,
          courseid:this.seldiscipline,
          subitem:this.subitem,
        }
        var self=this
        function callback(res){
          self.$confirm('', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'success'
          }).then(function(){
            window.location.reload()
          }).catch(function(){
              window.history.go(-1)
          })
        }
        xhttp.post('/question/addassociationquestion',data,callback,xhttp.errcodefn,xhttp.errfn)
      },
      deleteoneq:function(index){//
        var self=this
        self.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(function(){
          self.subitem=methods.deleteItem(self.subitem,index)
        }).catch(function(){

        })
      }
    }
  }

</script>
<style lang="scss">
  @import "../../assets/css/common.css";
  @import "../../assets/css/addqcommon.css";
  .setlabel{
    width:100px;
  }
  .setcontent{
    min-width:220px;
  }
  .zuhehandler{
    padding-top:20px;
  }
  .oneq{
    border:1px solid #ddd;
    padding:0 10px 20px 10px;
    margin:10px 0;
  }
  .oneq>div{
    position:relative;
  }
  .deleteoneq{
    position:absolute;
    top:10px;
    left:140px;
  }
</style>



