<template>
	<div>
        <div>
            <el-breadcrumb separator=">">
              <el-breadcrumb-item :to="{ path: '/examManage/exam_test' } ">&</el-breadcrumb-item>
              <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class = "exam-ope">
            <el-tabs v-model="activeName">
                <el-tab-pane label="" name="first">
                    <div class = "exam-ope-title">
                        <span class="exam-ope-title-item">{{name}}</span>
                        <span class="exam-ope-title-item">{{coursename}}</span>
                        <span class="exam-ope-title-item">{{type}}</span>
                        <span class="exam-ope-title-item">{{model}}</span>
                        <span class="exam-ope-title-item">{{starttime}}</span>
                        <span class="exam-ope-title-item">{{statusinfo | statusfn}}</span>
                        <span class="exam-ope-title-item">{{papername}}</span>
                        <span class="exam-ope-title-item">{{totalScore}}</span>
                    </div>
                    <div>
                        <div style="margin:10px 0;">:</div>
                        <el-table
                            ref="singleTable"
                            :data="itemlist"
                            border
                            style="width: 100%">
                        <el-table-column
                          type="index"
                          width="80"
                          label = "">
                        </el-table-column>
                        <el-table-column
                          property="questionname"
                          label=""
                          >
                        </el-table-column>
                        <el-table-column
                          property="coursename"
                          label=""
                          >
                        </el-table-column>
                        <el-table-column
                          property="tag"
                          label=""
                          >
                        </el-table-column>
                        <el-table-column
                          property="score"
                          label=""
                          >
                        </el-table-column>
                        </el-table>
                    </div>
                </el-tab-pane>
                <el-tab-pane label="" name="second">
                    <div class = "stu-range">
                        <span></span><span v-for="(item,index) in classlist">{{item.classname}}<span v-if="index!=classlist.length-1"></span></span><span style="margin-left:20px;">{{totalStu}}</span>
                    </div>
                    <div class = "eo-teacher-item"v-for = "(item,index) in teacherlist">
                        <span class = "eo-t-i-s">{{index+1}}:{{item.questionname}}</span>
                        <span class = "eo-t-i-s">{{item.starttime+''+item.endtime}}</span>
                        <span class = "eo-t-i-s">{{item.teachername+','+item.mobile}}</span>
                    </div>
                </el-tab-pane>
                <el-tab-pane v-if="creator == uid" label="" name="third">
                    <el-form :model = "queryForm" :inline = "true"   label-width ="80px">
                        <el-form-item label="" prop = "name" >
                            <el-input  v-model = "queryForm.stuname"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop = "no" >
                            <el-input  v-model = "queryForm.code"></el-input>
                        </el-form-item>
                         <el-button type = "primary" @click = "queryByStuinfo" > </el-button>
                         <el-button type = "primary" ></el-button>
                    </el-form>
                    <el-table
                    :data="scorelist"
                    border
                    style="width: 100%">
                        <el-table-column
                          type="index"
                          width="80"
                          label = "">
                        </el-table-column>
                        <el-table-column
                          property="name"
                          label=""
                          >
                        </el-table-column>
                        <el-table-column
                          property="code"
                          label=""
                          >
                        </el-table-column>
                        <el-table-column prop='totalscore' label = ""></el-table-column>
                        <el-table-column label="">
                            <el-table-column v-for = "item in questionlist" :prop = "item.learnname" :label = "item.learnname"></el-table-column>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>
                <el-tab-pane label="" name="fourth">
                    <el-form :model = "queryForm1" :inline = "true"   label-width ="90px">
                        <el-form-item label="" >
                            <el-select v-model="queryForm1.learnid" placeholder="">
                                <el-option label="" value=""></el-option>
                                <el-option
                                  v-for="item in learnlist"
                                  :key="item &&item.learnid"
                                  :label="item &&item.name"
                                  :value="item &&item.learnid">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="" prop = "name" >
                            <el-input  v-model = "queryForm1.stuname"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop = "no" >
                            <el-input  v-model = "queryForm1.code"></el-input>
                        </el-form-item>
                         <el-button type = "primary" @click = "queryByInfo" > </el-button>
                         <el-button type = "primary" ></el-button>
                    </el-form>
                    <el-table
                    :data="answerlist"
                    border
                    style="width: 100%">
                        <el-table-column
                          type="index"
                          width="80"
                          label = "">
                        </el-table-column>
                        <el-table-column
                          property="stuname"
                          label=""
                          >
                        </el-table-column>
                        <el-table-column
                          property="code"
                          label=""
                          >
                        </el-table-column>
                        <el-table-column
                          property="questionname"
                          label=""
                          >
                        </el-table-column>
                        <el-table-column
                          property="score"
                          label=" "
                          >
                        </el-table-column>
                        <el-table-column
                          property="score"
                          label=" "
                          >
                          <template scope = "scope">
                              <span v-if="scope.row.status==-1"></span>
                              <span v-if="scope.row.status==1"></span>
                              <span v-if="scope.row.status==2"></span>
                          </template>
                        </el-table-column>
                        <el-table-column
                          property="answertime"
                          label=" "
                          >
                        </el-table-column>
                        <el-table-column
                          property="gradetime"
                          label=" "
                          >
                        </el-table-column>
                        <el-table-column label="">
                          <template scope="scope">
                            <el-button type="text" v-if="scope.row.status==2" @click="toRateOperate(scope.$index, scope.row)"></el-button>
                            <el-button type="text" v-if="scope.row.status==1&&scope.row.teacherid==uid" @click="toRateOperate(scope.$index, scope.row)"></el-button>
                           </template>
                        </el-table-column>
                    </el-table>
                    <el-pagination
                    style="float:right"
                      @size-change = "handleSizeChange1"
                      :current-page.sync="pagenum1"
                      :page-sizes="[10, 20, 50, 100]"
                      :page-size="pagesize1"
                      layout="total, sizes, prev, pager, next, jumper"
                      :total="totalcount1">
                    </el-pagination>
                </el-tab-pane>
            </el-tabs>
        </div>
    </div>
</template>
<script >
import {post,setCookie,getCookie,setTitle} from '../../config/util'
	export default {
        data() {
            return {
                uid:'',

                activeName:'first',
                types:['','','',''],
                models:['','',''],
                examid:'',
                creator:'',
                name:'',
                coursename:'',
                type:'',
                model:'',
                starttime:'',
                statusinfo:'',
                papername:'',
                paperid:'',
                totalScore:'',
                pagenum:1,
                pagesize:10,
                totalcount:0,
                itemlist:[],
                teacherlist:[],
                classlist:[],
                scorelist:[],
                learnlist:[],
                learnidlist:[],
                questionlist:[],

                queryForm:{code:'',stuname:''},
                queryForm1:{learnid:'',code:'',stuname:''},
                answerlist:[],
                pagenum1:1,
                pagesize1:10,
                totalcount1:0,
            }
        },
        computed:{
            totalStu(){
                let sum=0;
                this.classlist.forEach(item=>{
                    sum+=item.total;
                });
                return  sum;
            }
        },
        components: {
        },
        created() {
        },
        filters:{
        	statusfn(value){
        		var status=value;
				switch (value){
					case 0: status="";
						break;
					case 1: status="";
						break;
					case 2: status="";
						break;
					case 3: status="";
						break;
					default:
						break;
				}
				return status;
        	}
        },
        mounted () {
            this.uid = getCookie('uid');
            this.examid = this.$route.params.examid;
            this.getExamInfo();
            this.getLearnList();
        },
        watch:{
            pagenum1(cval,oval){
                this.getAnswerlist({...this.queryForm1,pagenum:cval,pagesize:this.pagesize1})
            },
            pagesize1(cval,oval){
                this.getAnswerlist({...this.queryForm1,pagenum:1,pagesize:cval})
            }
        },
        methods: {
            getExamInfo(){
                let self = this;
                post('/unifyexam/queryexamdetail',{
                    command:'unifyexam/queryexamdetail',
                    loginid:getCookie('uid'),
                    sessionid:getCookie('sid'),
                    teacherid:getCookie('uid'),
                    examid:this.examid,
                }).done(data=>{
                    if(data&&data.errcode==0){
                        self.name = data.name;
                        self.coursename = data.coursename;
                        self.type = self.types[data.type];
                        self.model = self.models[data.model];
                        self.starttime = data.starttime&&data.endtime&&data.starttime.substr(0,19)+'~'+data.endtime.substr(11,19);
                        self.statusinfo=data.status;
                        self.totalScore=data.score;
                        self.papername = data.papername;
                        self.paperid = data.paperid;
                        self.teacherlist = data.iteamteacherlist;
                        self.classlist = data.classlist;
                        self.creator = data.creator;
                        self.getSelectList();
                        self.getTotalScore();
                        self.getAnswerlist({pagenum:1,pagesize:10});
                    }
                })
            },
            getSelectList(){
                let self = this;
                post('/exampaper/queryselectedquestion',{
                    command:'exampaper/queryselectedquestion',
                    sessionid: $.cookie('sid'),
                    loginid:$.cookie('uid'),
                    page:this.pagenum,
                    reqnum:this.pagesize,
                    paperid:self.paperid,
                    questiontypeid:7,
                    createperson:1,
                }).done(data=>{
                    if(data&&data.errcode==0){
                        console.log(data)
                        self.itemlist = data.questionlist;
                        console.log(self.itemlist)
                    }
                })
            },
            queryByStuinfo(){
                this.getTotalScore(this.queryForm);
            },
            getTotalScore(option){
                let self = this;
                post('/unifyexam/querystuoperatscore',{
                    command:'unifyexam/querystuoperatscore',
                    loginid:getCookie('uid'),
                    sessionid:getCookie('sid'),
                    teacherid:getCookie('uid'),
                    examid:this.examid,
                    ...option
                }).done(data=>{
                    if(data&&data.errcode==0){
                        self.scorelist = data.scorelist.map(item=>{
                            item.learnscorelist.forEach(it=>{
                                item[it.learnname]=it.score;
                            });
                            return item;
                        });
                        console.log('aaaaaaaaaaaaa')
                        console.log(self.scorelist)
                        if(data.scorelist.length>0){
                            self.questionlist = data.scorelist[0].learnscorelist.map(item=>{
                                return {learnname:item.learnname,learnid:item.learnid};
                            })
                            
                        }
                        
                    }
                })
            },
            getLearnList(){
                let self = this;
                post('/learn/querylearninfo',{
                    command:'learn/querylearninfo',
                    loginid:getCookie('uid'),
                    sessionid:getCookie('sid'),
                   page:1,
                   reqnum:1000
                }).done(data=>{
                    if(data&&data.errcode==0){
                        self.learnlist = data.learnids;
                       
                    }
                })
            },
            queryByInfo(){
                this.getAnswerlist({...this.queryForm1,pagenum:1,pagesize:10});
            },
            getAnswerlist(option){
                let self = this;
                post('/unifyexam/querystuanswerlist',{
                    command:'unifyexam/querystuanswerlist',
                    loginid:getCookie('uid'),
                    sessionid:getCookie('sid'),
                    teacherid:getCookie('uid'),
                    examid:this.examid,
                    ...option
                }).done(data=>{
                    if(data&&data.errcode==0){
                        self.answerlist = data.answerlist;
                        self.totalcount1 = data.totalcount;
                    }
                })
            },
            handleSizeChange(size){
                this.pagesize = size;
            },
            handleSizeChange1(size){
                this.pagesize1 = size;
            },
            toRateOperate(index,row){
                this.$router.push({name:'ratevideo',query:{...row,examid:this.examid}})
            },
            toRatedOperate(){

            },
        }
    }

</script>
<style scoped>
	.exam-ope{
        border: 1px solid #e1eaf1;
        box-shadow: 0 1px 3px 0 rgba(0,34,77,.05);
        background-clip: content-box;
        margin-top:20px;
        padding:20px;
    }
    .exam-ope-title{
        border-bottom:1px solid #e1eaf1;
        padding-bottom:10px;
    }
    .exam-ope-title-item{
        width:24%;
        padding:10px 0;
    }
    .stu-range{
       border-bottom:1px solid #e1eaf1;
        padding-bottom:10px;
    }
    .eo-teacher-item{
        padding:10px 0;

        border-bottom: 1px solid #e1eaf1;
    }
    .eo-t-i-s{
        width:40%;
        padding:10px;
    }
</style>


